<template>
  <div>
    <div v-if="!showEdit && !showAdd" class="container">
      <div class="header">
        <div class="title">公司列表</div>
        <el-button style="margin-left: 30px" @click="showAdd=!showAdd">新增设备类型</el-button>
      </div>
      <ceres-table
        :pagination="pagination"
        :table-column="tableColumn"
        :table-data="tableData"
        :operate="operate"
        @handleEdit="handleEdit"
        @handleDelete="handleDelete"
        @currentChange = "currentChange"
      />
    </div>

    <category-add v-if="showAdd" @hanleHide="handleHideAdd"/>
    <category-edit v-if="showEdit" :edit-i-d="editID" @hanleHide="handleHideEdit"/>
  </div>
</template>

<script type="text/ecmascript-6">
import CategoryModel from '@/models/category'
import CategoryAdd from './CategoryAdd'
import CategoryEdit from './CategoryEdit'

export default {
  name: 'CompanyList',
  components: {
    CategoryAdd,
    CategoryEdit
  },
  data() {
    return {
      tableColumn: [
        { prop: 'name', label: '名称' },
        { prop: 'specs', label: '容量规格' },
        { prop: 'total_capacity', label: '总容量' },
        { prop: 'elec_limit', label: '电流阈值' },
        { prop: 'warranty_year', label: '保证年限' },
      ],
      tableData: [],
      operate: [
        { name: '编辑', func: 'handleEdit', type: 'primary' },
        { name: '删除', func: 'handleDelete', type: 'danger' },
      ],
      showEdit: false,
      showAdd: false,
      pagination: {
        pageSize: 0,
        pageTotal: 0
      },
      editID: ''
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getCategoryList(1, 10)
  },
  methods: {
    async getCategoryList(page, size) {
      const res = await CategoryModel.getList(size, page)
      this.tableData = [...res.items]
      this.tableData.forEach(item => {
        let keys = ''
        item.specs.forEach((spec, index) => {
          keys += `${spec.key}:${spec.value}`
          if (index < item.specs.length - 1) {
            keys += ','
          }
        })
        item.specs = keys
      })
      this.pagination.pageTotal = res.total
      this.pagination.pageSize = res.size
    },
    handleEdit(val) {
      this.showEdit = true
      this.editID = val.row.id
    },
    async handleDelete(val) {
      const res = await CategoryModel.deleteOne(val.row.id)
      this.$message.success('删除成功')
      this.getCategoryList(1, 10)
    },
    handleHideAdd() {
      this.showAdd = false
      this.getCategoryList(1, 10)
    },
    handleHideEdit() {
      this.showEdit = false
      this.getCategoryList(1, 10)
    },
    // 切换当前页
    currentChange(page) {
      this.getCategoryList(page, 10)
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .container {
    .header {
      display flex
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
    }
  }
</style>
